.App {
  text-align: center;
}

.btn{
	/* text-transform: uppercase; */
	text-decoration: none;
  background: rgb(146, 233, 255);
	padding: 20px;
	border-radius: 5px;
	display: inline-block;
	border: none;
  position: fixed;
}

.btn:hover {
    /* color: transparent; */
    background-clip: text;
    background-size: 100%;
    background-position: 100% center;
    background-image: linear-gradient(45deg, rgb(147, 82, 255) 0%, rgb(0, 255, 240) 100%);
    background:linear-gradient(45deg, rgb(147, 82, 255) 0%, rgb(0, 255, 240) 100%)
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
